.single-info {
  display: flex;
  justify-content: space-between;
  padding-bottom: 25px;
  margin-bottom: 40px;
  border-bottom: 2px solid #E9E9E9;
}

.single-info .left {
  width: 65.8%;
  max-width: 670px;
}

.single-info .right {
  width: 31.7%;
}

.single-description_inner {
  margin-bottom: 25px;
  max-height: 133px;
  overflow: hidden;
}

.single-description_inner.active {
  max-height: 100%;
}

.single-description_inner h4 {
  font-size: 22px;
  font-weight: 500;
  line-height: 30px;
  margin-bottom: 8px;
}

.single-description_inner p {
  font-size: 17px;
  line-height: 26px;
  margin-bottom: 15px;
}

.single-description_inner p:last-child {
  margin-bottom: 0;
}

.single-description {
  padding-bottom: 40px;
  margin-bottom: 40px;
  border-bottom: 2px solid #E9E9E9;
}

.single-description .read-more {
  display: none;
  font-size: 15px;
  font-weight: 500;
  line-height: 20px;
  color: #23A8CD;
}

.single-feautures {
  padding-bottom: 40px;
  margin-bottom: 40px;
  border-bottom: 2px solid #E9E9E9;
}

.feauture-item {
  padding-left: 45px;
  position: relative;
  margin-bottom: 25px;
}

.feauture-item:last-child {
  margin-bottom: 0;
}

.feauture-item img {
  width: 32px;
  height: 32px;
  position: absolute;
  left: 0;
  top: 0;
}

.feauture-item h4 {
  font-size: 18px;
  font-weight: 500;
  line-height: 28px;
  margin-bottom: 4px;
}

.feauture-item p {
  font-size: 15px;
  line-height: 22px;
  max-width: 625px;
}

.accomodation-title {
  font-size: 22px;
  font-weight: 500;
  line-height: 30px;
  margin-bottom: 25px;
}

.accomodation-wrap {
  display: flex;
  flex-wrap: wrap;
}

.accomodation-item {
  width: 24%;
  border: 2px solid #E9E9E9;
  border-radius: 4px;
  padding: 25px 17px;
  margin-right: 1%;
  margin-bottom: 1%;
}

.accomodation-item:nth-child(4n) {
  margin-right: 0;
}

.accomodation-item img {
  display: block;
  width: 40px;
  height: 40px;
  margin-bottom: 20px;
  object-fit: contain;
}

.accomodation-item h4 {
  font-size: 18px;
  font-weight: 500;
  line-height: 28px;
  margin-bottom: 4px;
}

.accomodation-item p {
  font-size: 15px;
  line-height: 22px;
}

.specification {
  padding-bottom: 40px;
  margin-bottom: 40px;
  border-bottom: 2px solid #E9E9E9;
}

.spec-title {
  font-size: 22px;
  font-weight: 500;
  line-height: 30px;
  margin-bottom: 25px;
}

.specification-wrap {
  display: flex;
  flex-wrap: wrap;
}

.specification-wrap .column {
  width: 30%;
  margin-right: 30px;
  margin-bottom: 25px;
}

.specification-wrap .column:nth-child(3n) {
  margin-right: 0;
}

.specification-wrap .column:last-child, .specification-wrap .column:nth-last-child(2) {
  margin-bottom: 0;
}

.specification-wrap .column .spec-description {
  margin-bottom: 10px;
  max-height: 120px;
  overflow: hidden;
}

.specification-wrap .column .spec-description.active {
  max-height: 100%;
}

.specification-wrap .column h4 {
  font-size: 18px;
  font-weight: 500;
  line-height: 28px;
  margin-bottom: 4px;
}

.specification-wrap .column p {
  font-size: 15px;
  line-height: 22px;
}

.specification-wrap .column ul li {
  font-size: 15px;
  line-height: 22px;
}

.specification-wrap .view-specification {
  display: none;
  font-size: 15px;
  font-weight: 500;
  line-height: 20px;
  color: #23A8CD;
}

@media screen and (max-width: 990px) {
  .single-info {
    flex-direction: column;
  }
  .single-info .left {
    width: 100%;
  }
  .single-info .right {
    width: 100%;
  }
}

@media screen and (max-width: 500px) {
  .single-info {
    padding-bottom: 30px;
    margin-bottom: 30px;
  }
  .single-info .right .sidebar-message, .single-info .right .sidebar-bottom_text {
    display: none;
  }
  .single-info .right .order-sidebar {
    height: 0;
    overflow: hidden;
    box-shadow: none;
    padding: 0;
    margin: 0;
  }
  .single-info .right .order-sidebar .order-sidebar_title, .single-info .right .order-sidebar .charter-input, .single-info .right .order-sidebar .to-checkout_button {
    display: none;
  }
  .single-description_inner {
    margin-bottom: 15px;
  }
  .single-description_inner p {
    font-size: 16px;
    line-height: 23px;
  }
  .single-description {
    padding-bottom: 30px;
    margin-bottom: 30px;
  }
  .single-description .read-more {
    font-size: 14px;
  }
  .single-feautures {
    padding-bottom: 30px;
    margin-bottom: 30px;
  }
  .feauture-item {
    margin-bottom: 20px;
  }
  .feauture-item h4 {
    font-size: 17px;
    line-height: 26px;
  }
  .feauture-item p {
    font-size: 14px;
    line-height: 20px;
  }
  .accomodation-title {
    font-size: 21px;
    line-height: 30px;
    margin-bottom: 15px;
  }
  .accomodation-wrap {
    flex-wrap: wrap;
  }
  .accomodation-item {
    width: 48%;
    margin-bottom: 15px;
  }
  .accomodation-item:nth-last-child(2), .accomodation-item:last-child {
    margin-bottom: 0;
  }
  .accomodation-item h4 {
    font-size: 16px;
    line-height: 23px;
  }
  .accomodation-item p {
    font-size: 14px;
    line-height: 20px;
  }
  .spec-title {
    font-size: 21px;
    line-height: 30px;
    margin-bottom: 15px;
  }
  .specification {
    padding-bottom: 30px;
    margin-bottom: 30px;
  }
  .specification-wrap .column {
    width: 100%;
    margin-right: 0;
    margin-bottom: 15px;
  }
  .specification-wrap .column:nth-last-child(2) {
    margin-bottom: 15px;
  }
  .specification-wrap .column .spec-description {
    margin-bottom: 8px;
    max-height: 110px;
  }
  .specification-wrap .column h4 {
    font-size: 17px;
    line-height: 26px;
    margin-bottom: 2px;
  }
  .specification-wrap .column p {
    font-size: 14px;
    line-height: 20px;
  }
  .specification-wrap .column ul li {
    font-size: 14px;
    line-height: 20px;
  }
  .specification-wrap .view-specification {
    font-size: 14px;
    line-height: 20px;
  }
}

